import React, { useRef, useState } from 'react'
import Pagination from "react-js-pagination";
import MonthSelect from '../../../../components/common/Select/MonthSelect';
import YearSelect from '../../../../components/common/Select/YearSelect';
import RegionSelect from '../../../../components/common/Select/RegionSelect';
import SearchInput from '../../../../components/UI/Atoms/Input/SearchInput';
import PageContainer from '../../../../components/layout/PageContainer';
import { useSelect } from '../../../../hooks/basic/useSelect';
import { getCurrMonth, getCurrYear } from '../../../../utils/basic/getDate';
import { Grid } from '../../../../components/common/Grid/Grid';
import Description from '../../../../components/UI/Atoms/Ex/Description';
import Em from '../../../../components/UI/Atoms/Ex/Em';
import { useMutation, useQuery } from '@tanstack/react-query';
import { useInput } from '../../../../hooks/input/useInput';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { getAssociationLevelupList } from '../../../../api/Association/levelupAPI';
import { useRecoilValue } from 'recoil';
import assoInfoAtom from '../../../../recoil/atom/association/assoInfoAtom';
import { getFullname } from '../../../../utils/render/getFullname';
import RankImage from '../../../../components/common/Rank/RankImage';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';
import ModalContainerLV from '../../../../components/layout/ModalContainerLV';
import { useModal } from '../../../../hooks/basic/useModal';
import LevelupCertificate from '../../../../components/common/Levelup/LevelupCertificate';
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage';
import { toAge } from '../../../../utils/render/toAge';

const LevelupList = () => {
  const [page, setPage] = useState(1)
  let yy = getCurrYear().toString()
  let mm = getCurrMonth().toString()

  // recoil
  const assoInfo = useRecoilValue(assoInfoAtom)

  const asso = assoInfo.name === '한국' ? -1 : assoInfo.name

  const [totalData, setTotalData] = useState('')

  // custom hook
  const [ year, handleYear ] = useSelect(yy)
  const [ month, handleMonth ] = useSelect(mm)
  const [ region, handleRegion ] = useSelect(asso)
  const [openCert, openCertFn, closeCertFn] = useModal()
  
  const [keyword, setKeyword] = useState('')

  // query
  const { data } = useQuery(['getAssociationLevelupList', year, month, region, page], () => getAssociationLevelupList(year, month, region, page))
  // const { mutate: SearchMt } = useMutation((keyword) => )

  // // handler
  // const handleKeyword = e => {
  //   // keyword.current = e.target.value
  //   console.log(e.target.value)
  //   setKeyword(e.target.value)
  // }

  // const handleSearchClick = e => {
  //   // console.log(year, month, region, keyword, page)
  // }

  const handleCertificate = (e) => {
    let index = e.target.id;
    setTotalData(data?.levelup_info[index])
    // console.log(data[index])
    openCertFn()
  }

  // useDebug(data)


  return (
    <>
      <PageContainer>
        <div className='flex justify-between items-center mb-3 w-full'>
          <div className="flex gap-[5px]">
            <YearSelect value={year} onChange={handleYear} />
            <MonthSelect value={month} onChange={handleMonth} />
            { assoInfo?.name === '한국' &&<RegionSelect list={assoInfo?.region_info} value={region} onChange={handleRegion} /> }
          </div>
          {/* <SearchInput 
            width='250px'
            placeholder='도장 이름 검색'
            value={keyword}
            onChange={handleKeyword}
            onClick={handleSearchClick}
          /> */}
        </div>
        <Grid>
          <Grid.Head>
            <Description>{year}년 {month}월 전체 승급자는 <Em>{data?.count}명</Em> 입니다.</Description>
          </Grid.Head>
          <Grid.RowWrap>
            {/* <Grid.Row className='h-[50px] bg-[#fafafa]'>
              <Grid.Col fixed='225px'>도장 이름</Grid.Col>
              <Grid.Col fixed='210px'>수련생</Grid.Col>
              <Grid.Col fixed='100px'>나이</Grid.Col>
              <Grid.Col fixed='220px'>승급 급수</Grid.Col>
              <Grid.Col fixed='100px'>승급증</Grid.Col>
            </Grid.Row> */}
            { React.Children.toArray(
              data?.levelup_info?.map((item, idx) =>
              <Grid.Row className="px-[20px]" >
                <Grid.Col fixed='150px' className='p-0'>{item?.dojang_name}</Grid.Col>
                <Grid.Col fixed='50px' className='p-0'><ProfileImage urlKey={item?.photo_url}/></Grid.Col>
                <Grid.Col fixed='130px' className='p-0'>{getFullname(item?.last_name, item?.first_name)}</Grid.Col>
                <Grid.Col fixed='60px' className='p-0 pt-[5px]'><RankImage src={item?.level_before} /></Grid.Col>
                <Grid.Col fixed='45px' className='p-0'>{item?.sex === 1 && '남자' || item?.sex === 2 && '여자'}</Grid.Col>
                <Grid.Col fixed='60px' className='p-0'>{toAge(item?.dob)}</Grid.Col>
                <Grid.Col fixed='130px' className='p-0'>
                  <div className='flex flex-col gap-[2px]'>
                    <span className='text-[12px] text-[#5E81F4]'>승급일</span>
                    <span>{item?.levelup_date?.substr(0, 10) || '　'}</span>
                  </div>
                </Grid.Col>
                <Grid.Col fixed='115px' className='p-0'>
                  <div className='flex flex-col gap-[2px]'>
                    <span className='text-[12px] text-[#5E81F4]'>승급 급수</span>
                    <span className='flex items-center'>{item?.level_after} <RankImage src={item?.level_after} className="ml-[5px]" /></span>
                  </div>
                </Grid.Col>
                <Grid.Col fixed='80px' className='p-0'>
                  <BlackBtnSM className='w-[100px]' id={`${idx}`} onClick={handleCertificate}>승급증 보기</BlackBtnSM>
                </Grid.Col>
              </Grid.Row>  
              )
            )}
          </Grid.RowWrap>
        </Grid>

        <ModalContainerLV
          open={openCert}
          onClose={closeCertFn}
          doc
        >
          <LevelupCertificate data={totalData} />
        </ModalContainerLV>

        <Pagination 
          activePage={page}
          itemsCountPerPage={30}
          totalItemsCount={Math.ceil(data?.count/1)}
          pageRangeDisplayed={5}
          prevPageText={'‹'}
          nextPageText={'›'}
          onChange={setPage}
        />
      </PageContainer>
    </>
  )
}

export default LevelupList